import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import _ from 'lodash'
import s from './index.module.styl'

const titleText = "Adam Wagner"
const metadata = [
        { name: 'description', content: 'Sample' },
        { name: 'keywords', content: 'sample, something' },
      ]


class TemplateWrapper extends React.Component {


  componentDidMount() {
    let pageName = _.startCase(window.location.pathname).replace('/', '')
    this.setState({pageName: pageName})
  }

  render() {
    return (
      <div style={{width: '100%'}}>
        <Helmet title={titleText} meta={metadata} >
          <link href="https://fonts.googleapis.com/css?family=Lato:300,300i,400,400i,700,900" rel="stylesheet" />
          <link href="https://fonts.googleapis.com/css?family=Lora:700" rel="stylesheet" />

          <link rel="apple-touch-icon" sizes="57x57" href="/static/images/favicons/apple-icon-57x57.png"/>
          <link rel="apple-touch-icon" sizes="60x60" href="/static/images/favicons/apple-icon-60x60.png"/>
          <link rel="apple-touch-icon" sizes="72x72" href="/static/images/favicons/apple-icon-72x72.png"/>
          <link rel="apple-touch-icon" sizes="76x76" href="/static/images/favicons/apple-icon-76x76.png"/>
          <link rel="apple-touch-icon" sizes="114x114" href="/static/images/favicons/apple-icon-114x114.png"/>
          <link rel="apple-touch-icon" sizes="120x120" href="/static/images/favicons/apple-icon-120x120.png"/>
          <link rel="apple-touch-icon" sizes="144x144" href="/static/images/favicons/apple-icon-144x144.png"/>
          <link rel="apple-touch-icon" sizes="152x152" href="/static/images/favicons/apple-icon-152x152.png"/>
          <link rel="apple-touch-icon" sizes="180x180" href="/static/images/favicons/apple-icon-180x180.png"/>
          <link rel="icon" type="image/png" sizes="192x192"  href="/static/images/favicons/android-icon-192x192.png"/>
          <link rel="icon" type="image/png" sizes="32x32" href="/static/images/favicons/favicon-32x32.png"/>
          <link rel="icon" type="image/png" sizes="96x96" href="/static/images/favicons/favicon-96x96.png"/>
          <link rel="icon" type="image/png" sizes="16x16" href="/static/images/favicons/favicon-16x16.png"/>



          <style type="text/css">{`

            @font-face {
              	font-family: 'Fakt Pro Nor';
              	src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
                url(data:application/font-woff;charset=utf-8;base64,) format('woff'),
              		url('FaktPro-Normal.ttf') format('truetype');
              	font-weight: normal;
              	font-style: normal;
            }

            @font-face {
            	font-family: 'Fakt Pro Nor Ita';
            	src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
            		url(data:application/font-woff;charset=utf-8;base64,) format('woff'),
            		url('FaktPro-NormalItalic.ttf') format('truetype');
            	font-weight: normal;
            	font-style: italic;
            }

            @font-face {
            	font-family: 'Fakt Pro SmBd';
            	src:         url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
            		url(data:application/font-woff;charset=utf-8;base64,) format('woff'),
            		url('FaktPro-SemiBold.ttf') format('truetype');
            	font-weight: 600;
            	font-style: normal;
            }


          `}</style>
        </Helmet>

          <canvas className={s.canvas} id="canvas"></canvas>
          <div>
            {this.props.children()}
          </div>


      </div>
    )
  }
}

TemplateWrapper.propTypes = {
  children: PropTypes.func,
}


export default TemplateWrapper



